<script>
    import Rythm from "rythm.js";

    const rythm = new Rythm();

    const music = require("../assets/v.mp3");

    export default {
        name: "my",
        data() {
            return {
                isOk: true,
            }
        },
        mounted() {
            this.initRythm();
        },
        methods: {
            initRythm() {
                rythm.setMusic(music);
                // rythm.addRythm('name1', 'shake', 0, 10, {
                //     direction: 'left'
                // });

                rythm.addRythm('name1', 'twist', 0, 10)

                rythm.addRythm('name2', 'shake', 0, 10, {
                    direction: 'left'
                })
                rythm.addRythm('name3', 'jump', 150, 40, {
                    min: -20,
                    max: 20
                })

                //rythm.start();


                // rythm.addRythm('name1', 'tilt', 0, 10)
                // rythm.addRythm('name2', 'tilt', 0, 10)

            },
            nameClick() {
                this.isOk = !this.isOk;
                this.isOk ? rythm.stop() : rythm.start()
            }


        }
    }
</script>

